import React, {
  useMemo,
  useState,
} from "react";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/styles/ag-grid.css";
import "ag-grid-community/styles/ag-theme-quartz.css";
import "./App.css";
import imageCaptionJson from "./benchs-image-f.json";
import speechRecognitionJson from "./benchs-speech-f.json";
import visualQuestioningJson from "./benchs-vqa-f.json";

function App() {

  // Row Data: The data to be displayed.
  const [rowData, setRowData] = useState(imageCaptionJson);

  // Column Definitions: Defines the columns to be displayed.
  const [colDefs, setColDefs] = useState([
    { field: "dataset" },
    { field: "dataset_home", headerName: 'Home Page',  },
    { field: "description", cellEditor: "agLargeTextCellEditor",
      cellEditorPopup: true, editable: true },
    { field: "total_num", headerName: 'Total num (inaccurate)'  },
    { field: "short_description" },
    { field: "benchmarks",  },
    { field: "dataloaders" },
    { field: "best_models" },
  ]);

  const defaultColDef = useMemo(() => {
    return {
      filter: "agTextColumnFilter",
      floatingFilter: true,
      flex: 1,
      // editable: true,
      tooltipShowDelay: 500,
    };
  }, []);

  const setSelectedJson = (selectedJson) => {
    if (selectedJson === "imageCaptionJson") {
      setRowData(imageCaptionJson);
    }else if (selectedJson === "speechRecognitionJson") {
      setRowData(speechRecognitionJson);
    }else if (selectedJson === "visualQuestioningJson") {
      setRowData(visualQuestioningJson);
    }}


  return (
    <div className="App">
      <div >
        <h4 className="row">Datasets
        </h4>

        <select onChange={e => setSelectedJson(e.target.value)}>
          <option value="imageCaptionJson">Image Caption</option>
          <option value="speechRecognitionJson">Speech Recognition</option>
          <option value="visualQuestioningJson">Visual Questioning</option>
        </select>
         <a href='https://paperswithcode.com/datasets'> Source: https://paperswithcode.com/datasets
          </a>
      </div>
      <div
        className="ag-theme-quartz" // applying the Data Grid theme
        style={{ height: '100vh' }} // the Data Grid will fill the size of the parent container
      >
        <AgGridReact
          rowData={rowData}
          columnDefs={colDefs}
          defaultColDef={defaultColDef}
          enableCellTextSelection="true"
          ensureDomOrder="true"
        />
      </div>
    </div>
  );
}

export default App;
